@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700&display=swap");
body {
  background-color: #032F35;
  background-position: bottom;
  background-position-y: 140%;
  background-repeat: no-repeat;
  background-size: contain;
  color: #000;
  font-family: "Raleway", sans-serif;
}

#login {
  width: 370px;
}
#login #login_error {
  color: #000;
}
#login .message {
  color: #000;
}
#login h1 {
  margin-bottom: 40px;
}
#login h1 a {
  width: 200px;
  height: 108.5px;
  background-size: 200px 108.5px;
}
#login h1 a:focus {
  box-shadow: none;
}
#login form {
  @apply rounded-3xl shadow-none border border-light-green p-10 bg-white;
}
#login form label {
  @apply text-sm text-dark-green mb-2;
}
#login form .input {
  @apply w-full border border-green/20 py-3 px-6 rounded-full font-sans text-base text-dark-green;
}
#login form .input::placeholder {
  @apply font-sans text-gray-300 text-base;
}
#login form .input:focus-visible {
  @apply outline-lime-green;
}
#login form input[type=submit] {
  @apply font-sans text-dark-green text-sm transition-all duration-500 ease-in-out inline-flex justify-center items-center gap-2 bg-lime-green uppercase font-bold py-2 px-11 rounded-full hover:text-white hover:bg-green border-0;
}
#login #backtoblog, #login #nav {
  text-align: center;
  color: #E6EADC;
}
#login #backtoblog a, #login #nav a {
  color: #E6EADC;
  font-weight: 600;
}
#login input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
  -webkit-text-fill-color: #000 !important;
}

.wpml-login-ls form#wpml-login-ls-form select#wpml-language-switcher-locales {
  @apply rounded-none border border-solid uppercase text-xs font-sans font-semibold transition-all duration-300 ease-in hover:bg-white;
}
.wpml-login-ls form#wpml-login-ls-form input.button {
  @apply rounded-none border border-solid uppercase text-xs font-sans font-semibold px-4 py-1 transition-all duration-300 ease-in hover:bg-white;
}